export default{
	data:function(){
		return {
			pageLoad:false,
			pageData:""
			
		}
	},
	onLoad:function(){
		this.getPage();
	},
	methods:{
		getPage:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/index.php?m=invite&a=my",
				success:function(res){
					that.pageData=res.data;
					that.pageLoad=true;
				}
			})
		}
	}
}
